
<template>
  <div
    style="background-color: #F6F6F6;position:fixed;top:50px;bottom:0;left:0;right:0;overflow-y:scroll;"
  >
    <div class="box">
      <div class="box_1">
        <p>退租时间</p>
        <p>2019-11-15</p>
      </div>
      <div class="box_1">
        <p>合约租期</p>
        <p>2019-11-15至2019-11-30</p>
      </div>
      <div class="box_1">
        <p>实际租期</p>
        <p>2019-11-15至2019-11-30</p>
      </div>
    </div>
    <div class="box_1">
      <p>退押金</p>
      <p>
        1000&ensp;
        <span style="font-size:12px;color:#B2B2B2;">元</span>
      </p>
    </div>
    <div class="rent">确认退租</div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  created() {
    this.$store.commit("setHeader", "退租");
  },
  methods: {},
  watch: {}
};
</script>
<style scoped>
.box {
  margin: 15px 0;
}
.box_1 {
  display: flex;
  background: #fff;
  line-height: 30px;
  font-size: 14px;
  color: #000;
  justify-content: space-between;
  padding: 4px 10px;
  border: 1px solid #e5e5e5;
}
.rent {
  background: #fb9e58;
  line-height: 40px;
  color: #fff;
  margin: 50px auto;
  width: 80%;
  border-radius: 5px;
  font-size: 14px;
}
</style>



